@use './scss/' as *;
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;900&display=swap');

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html{
  font-size: 100%;

  @include tablet{
    font-size: 80%;
  }
  @include mobile{
    font-size: 60%;
  }
}

body{
  font-family: $font-family;
  font-weight: 400;
  line-height: 1.5;

  background-color: $body-bg;
  color: $txt-color;

  @include mobile{
    margin-bottom: 3rem;
  }

}


a{
  text-decoration: none;
  color: unset;
}

@media (hover:hover) {
  a:hover{
    color:$main-color;
  }
}

button,input{
  outline: 0;
}

ul{
  list-style-type: none;
}
img{
  max-width: 100%;
}

iframe{
  border: 0;
}
.mb-3{
  margin-bottom: 3rem;
}
.mb-2{
  margin-bottom: 2rem;
}